import pick from 'lodash.pick';
import { writable } from 'svelte/store';
import * as THREE from 'three';
import { nodeCounter, nodeCreatorMap } from '../interface/program/nodes';
import { isNode, setAllUniforms } from '../interface/program/utils';
import '../interface/types/nodes';
import { buildProgramShader } from '../shader/builder/programBuilder';
import '../shader/types/core';
import { shaderMaterial$ } from './shaderStore';
export const PROGRAM_STORAGE_KEY = 'program';
// TODO: simplify, only one store should be necessary
export const programStore$ = writable();
export const programHistoryStore$ = writable();
export const setProgram = (program) => {
    programStore$.set(program);
    programHistoryStore$.update(({ history }) => {
        return {
            program,
            history
        };
    });
    updateShaderMaterial(program);
    pushProgram();
};
const updateShaderMaterial = (program) => {
    const shader = buildProgramShader(program);
    const material = new THREE.ShaderMaterial(shader);
    shaderMaterial$.set(material);
    return material;
};
export const subscribeToProgram = (subscriber) => {
    // TODO: for some reason is subscriber not notified when updating from loading certain programs
    programStore$.subscribe(subscriber);
};
export const initializeProgramStore = (program) => {
    try {
        programStore$.set(program);
        const material = updateShaderMaterial(program);
        programHistoryStore$.set({
            program,
            history: Array(2).fill({
                encodedProgram: encodeProgram(program),
                shaderMaterial: material
            }), // Duplicate first entry is a bit ugly, but it makes the logic simpler
        });
        // Make sure new nodes receive appropriate IDs
        nodeCounter.set(program.nodes.reduce((acc, node) => Math.max(Number.parseInt(node.id), acc), 0) + 1);
    }
    catch (error) {
        console.log(error);
    }
};
export const loadProgramFromString = (programData) => {
    try {
        const program = decodeProgram(programData);
        if (!program)
            return undefined;
        initializeProgramStore(program);
        return program;
    }
    catch (err) {
        return undefined;
    }
};
export const encodeProgram = (program) => {
    // JSON stringify will expand on all node connections, meaning there will be copies/duplicates of a lot of connected nodes
    // Therefore, we need to iterate over all the nodes in the copy and replace the links with a reference to the corresponding node ID
    // Then convert the data back to JSON
    const rootId = program.rootNode.id;
    // All copied nodes
    const nodes = new Map();
    program.nodes.forEach(node => {
        nodes.set(node.id, JSON.parse(JSON.stringify(node)));
    });
    // Replace references with IDs
    for (const node of nodes.values()) {
        if (node.anchor) {
            node.anchor.active = false;
            node.anchor.hovered = false;
        }
        Object.values(node.fields).forEach((field) => {
            field.anchor.active = false;
            field.anchor.hovered = false;
            if (isNode(field.value)) {
                field.value = { nodeId: field.value.id };
            }
        });
    }
    const json = JSON.stringify(Object.assign({ rootId, nodes: Object.fromEntries(nodes.entries()) }, pick(program, [
        'position',
        'zoom'
    ])), null, 2);
    return json;
};
export const decodeProgram = (programData) => {
    const defaultNodeMap = new Map();
    let encodedProgram;
    if (typeof programData === 'string') {
        try {
            encodedProgram = JSON.parse(programData);
        }
        catch (error) {
            return undefined;
        }
    }
    else {
        encodedProgram = programData;
    }
    if (!encodedProgram.rootId || !encodedProgram.nodes)
        return undefined;
    const nodes = encodedProgram.nodes;
    // Expand nodes
    try {
        Object.values(nodes).forEach(node => {
            Object.values(node.fields).forEach((field) => {
                if (field.value && field.value.nodeId) {
                    const connectedNodeId = field.value.nodeId;
                    const connectedNode = nodes[connectedNodeId];
                    if (!connectedNode)
                        throw new Error(connectedNodeId);
                    field.value = connectedNode;
                }
            });
            if (!defaultNodeMap.has(node.type)) {
                defaultNodeMap.set(node.type, nodeCreatorMap[node.type](0, 0));
            }
            const defaultNode = defaultNodeMap.get(node.type);
            node.fields = Object.assign(Object.assign({}, defaultNode.fields), node.fields);
        });
    }
    catch (error) {
        return undefined;
    }
    const program = {
        openConnection: undefined,
        position: encodedProgram.position,
        zoom: encodedProgram.zoom,
        rootNode: nodes[encodedProgram.rootId],
        nodes: Object.values(nodes),
    };
    return program;
};
const updateLocalStorage = (encodedProgram) => {
    localStorage.setItem(PROGRAM_STORAGE_KEY, encodedProgram);
};
export const pushProgram = () => {
    shaderMaterial$.subscribe(material => {
        if (!material)
            return;
        programHistoryStore$.update(({ program, history }) => {
            if (!program)
                return { program, history };
            const encodedProgram = encodeProgram(program);
            updateLocalStorage(encodedProgram);
            return {
                program,
                history: [...history, {
                        encodedProgram,
                        shaderMaterial: material
                    }],
            };
        });
    })();
};
export const popProgram = () => {
    programHistoryStore$.update(({ program, history }) => {
        if (!history || history.length < 2)
            return { program, history };
        history.pop();
        const { encodedProgram, shaderMaterial } = history[history.length - 1];
        const previousProgram = Object.assign(Object.assign({}, decodeProgram(encodedProgram)), pick(program, 'zoom', 'position'));
        programStore$.set(previousProgram);
        shaderMaterial$.set(shaderMaterial);
        setAllUniforms(previousProgram, shaderMaterial);
        updateLocalStorage(encodedProgram);
        return {
            program: previousProgram,
            history
        };
    });
};
